* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

body{
  color: black;
  margin: 0px;
  padding: 0px;
  background-image: url('https://images.freeimages.com/images/large-previews/638/wood-wall-for-background-1634466.jpg');
}
/* .body::before {content: ""; background-image: url('https://images.freeimages.com/images/large-previews/638/wood-wall-for-background-1634466.jpg'); opacity: 0.5; top: 0; left: 0; bottom: 0; right: 0; position: absolute;} */


.App {
  text-align: center;
  width: 98vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
